@import url(https://fonts.googleapis.com/css?family=Open+Sans:600,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
body {
  font-family: "Open Sans", sans-serif;
  font-size: calc(1vh + 1vw + 1vmin);
  background-color: #f4f4f4; }

.title {
  font-size: 2em;
  font-weight: 800;
  margin-bottom: -1.3em; }

.event-container {
  width: 80vw;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  border-radius: 1.2em;
  padding: 0.5em 1.5em;
  margin-bottom: 0.5em;
  text-decoration: none;
  color: black;
  font-family: "Ubuntu", sans-serif; }
  .event-container--soon {
    background-color: #f9d3cb; }
    .event-container--soon .event-name {
      color: #fa7062; }
    .event-container--soon .event-date {
      color: #fa7062; }
  .event-container--not-soon {
    background-color: #e1f1f2; }
    .event-container--not-soon .event-name {
      color: #2faab4; }
    .event-container--not-soon .event-date {
      color: #2faab4; }
  .event-container .event-name {
    /* background-color: greenyellow; */
    font-weight: 700;
    font-size: 1.1em;
    line-height: 0.9em; }
  .event-container .event-date {
    /* background-color: green; */
    display: flex;
    align-items: flex-end;
    font-size: 0.7em;
    padding-bottom: 0.15em; }
  .event-container .image-column {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 4em;
    height: 4em; }
  .event-container .info-column {
    width: 80%;
    margin-left: 1em; }
  .event-container .event-image {
    max-width: 100%;
    max-height: 100%;
    border-radius: 50%; }
  .event-container .event-status {
    padding-top: 0.2em;
    color: grey;
    line-height: 1.1em;
    font-size: 0.7em;
    /* background-color: blueviolet; */ }

.date-break {
  font-size: 2em;
  font-weight: 800;
  margin-top: 1em;
  margin-bottom: 0.5em;
  padding-left: 2.2rem;
  margin-left: 3rem; }
  .date-break--soon {
    box-shadow: inset 0 -0.35rem 0 rgba(255, 74, 110, 0.2); }
  .date-break--not-soon {
    box-shadow: inset 0 -0.35rem 0 rgba(35, 209, 153, 0.2); }

